@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;;
  color: #fff;
}
body{
  background: linear-gradient(to right, #439CFA, #469DFC);
}
.App{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
}
.header{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 20px;
}
.description p{
  padding: 3px 0;
}
.form button, input{
  background-color: #1E75F8;
  outline: none;
  border: none;
  padding: 10px 20px;
  font-size: 15px;
  border-radius: 30px;
  margin: 0 2px;
  transition: all .5s;
}
.form button:hover{
  cursor: pointer;
  box-shadow: 0 0 5px #1E75F8;
}
.form input::-webkit-input-placeholder{
  color: #fff;
}
.card{
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.subjectImage{
  width: 350px;
  height: 350px;
  border-radius: 20px;
}
.subject{
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 30px;
}
::-webkit-scrollbar{
  width: 1vw;
  background-color: #fff;
}
::-webkit-scrollbar-thumb{
  background-color: #469DFC;
  border-radius: 20px;
}
@media (max-width: 1250px) {
  .subject{
    grid-template-columns: auto auto;
  }
}
@media (max-width: 830px) {
  .subject{
    grid-template-columns: auto;
  }
  .header{
    flex-direction: column;
    gap: 20px;
  }
}
@media (max-width: 425px) {
  .subjectImage{
    width: 300px;
    height: 300px;
  }
  .header h1{
    font-size: 25px;
  }
}
@media (max-width: 360px) {
  .subjectImage{
    width: 270px;
    height: 270px;
  }
}